@media screen and (max-width: 768px) {
  .container {
    min-height: 600px;
    width: 90%;
  }

  .form-container {
    width: 100%;
  }

  .overlay-container {
    display: none;
  }

  .sign-up-container {
    opacity: 1;
    z-index: 5;
  }

  .sign-in-container {
    z-index: 2;
  }
}

input:focus,
button:focus {
  outline: 2px solid #ff4b2b;
}

button:hover {
  opacity: 0.9;
}
.message {
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000;
  width: 80%;
  max-width: 400px;
  text-align: center;
}

.message.error {
  background-color: #ffebee;
  color: #c62828;
}

.message.success {
  background-color: #e8f5e9;
  color: #2e7d32;
}